.body{
    background-color: #ffde2a;
    height: 85vh;
}

.player{
    background-color: #840331;
    height: 200px;
    width: 150px;
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    border: 10px solid #000000;
    border-bottom: none;
    border-radius: 60px 80px 0 0;
    z-index: 1;
}

.player:after{
    content: '';
    position: absolute;
    width: 92%;
    height: 85%;
    background-color: #ea1616;
    top: 2.5px;
    left: 6px;
    border-radius: 58% 70% 28% 42% / 28% 56% 88% 89%;
}

.legs{
    height: 50px;
    width: 60px;
    background-color: #840331;
    position: absolute;
    bottom:  -50px;
    left: -10px;
    border: 10px solid #000000;
    border-top: none;
    border-radius: 0 0 22px 22px;
    z-index: 1;
}

.legs:before{
    content: "";
    height: 36px;
    width: 42px;
    background-color: #840331;
    position: absolute;
    
    right: -100px;
    border: 10px solid #000000;
    border-top: none;
    border-radius: 0 0 22px 22px;
}

.greenlegs{
    background-color: #0a4d2e;
}

.greenlegs:before{
    background-color: #0a4d2e;
}

.legs:after{
    content: "";
    height: 10px;
    width: 55px;
    background-color: #000000;
    position: absolute;
    top: -10px;
    left: 40px;
    border-radius: 0 0 10px 0 ;
}

.back{
    height: 120px;
    width: 35px;
    background-color: #ea1616;
    border: 10px solid black;
    position: absolute;
    left: -45px;
    top: 65px;
    border-right: none;
    border-radius: 20px 0 0 20px;
}

.back:before{
    content: '';
    position: absolute;
    height: 78px;
    width: 26px;
    background-color: #840331;
    bottom: -0.5px;
    left: -1px;
    border-radius: 12px 0 0 8px;
}

.greenback{
    background-color: #117f2d;
}

.greenback:before{
    background-color: #0a4d2e;
}

.glass{
    height: 75px;
    width: 110px;
    background-color: #225381;
    z-index: 2;
    position: absolute;
    top: 30px;
    left: 40px;
    border-radius: 25px 50px 30px 30px;
    border: 10px solid black ;
}

.glass:before{
    content: '';
    position: absolute;
    width: 85%;
    height: 65%;
    background-color: #71d4ec;
    border-radius: 0 28px 3px 30px;
    right: 0;
    top: 0;
}
.glass:after{
    content: '';
    position: absolute;
    width: 45%;
    height: 22%;
    background-color: #f7f7f7;
    left: 39px;
    top: 5px;
    border-radius: 10px ;
    transform: rotate(6deg);
}

.shadow{
    height: 40px;
    width: 210px;
    background-color: rgba(153, 130, 0, 0.2);
    position: absolute;
    bottom: -60px;
    right: -40px;
    border-radius: 50%;
    z-index: 0;
}

.green{
    background-color: #0a4d2e;
}

.green:after{
    background-color: #117f2d;
}

.options{
    display: flex;
    height: 15vh;
    width: 100%;
    justify-content: center;
    background-color: #ffde2a;
}

button{
    margin: 20px;
    /* padding: 10px; */
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: none;
    outline: none;
    /* box-shadow: 0 0 4px 4px #ffd900; */
    color: #ea1616;
    cursor: pointer;
}

button:hover{
    box-shadow: 0px 0px 16px 18px #ffde2a;
}

.link-a{
    padding-top: 4px;
    color: white;
    transition: 1s;
}

.link{
    background-color: #0077b5;
    color: white;
    font-size: 20px;
    
}

.git{
    background-color: rgb(36, 35, 35);
    font-size: 20px;
}

.link-a:hover{
    transform: scale(1.2);
}