@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins;
}

.home, .reports, .products{
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
section{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #001d30;
    -webkit-animation: changeBg 5s linear infinite;
            animation: changeBg 5s linear infinite;
}

@-webkit-keyframes changeBg
{
    to{
        -webkit-filter: hue-rotate(360deg);
                filter: hue-rotate(360deg);
    }
}

@keyframes changeBg
{
    to{
        -webkit-filter: hue-rotate(360deg);
                filter: hue-rotate(360deg);
    }
}

.loader{
    position: absolute;
    width: 150px;
    height: 150px;
    background: linear-gradient(to top, #001d30 40%, #51eeee);
    border-radius: 50%;
    -webkit-animation: rotate 1s linear infinite;
            animation: rotate 1s linear infinite;
}

@-webkit-keyframes rotate{
    to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate{
    to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.loader::before{
    content: '';
    position: absolute;
    background: #001d30;
    inset: 20px 20px 0 0;
    border-radius: 50%;
}

.loader::after{
    content: '';
    position: absolute;
    top: 39%;
    right: -8px;
    width: 40px;
    height: 40px;
    background: #51eeee;
    border-radius: 50%;
    box-shadow: 0 0 5px #51eeee, 0 0 25px #51eeee, 0 0 50px #51eeee, 0 0 75px #51eeee;
}


.navbar{
    background-color: #ff4fb0;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu{
    background-color: #ff4fb0;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active{
    left: 0;
    transition: 350ms;
}

.nav-text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a{
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover{
    background-color: #ff8ad8;
}

.nav-menu-items{
    width: 100%;
}

.navbar-toggle{
    background-color: #ff4fb0;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span{
    margin-left: 16px;
}
.body{
    background-color: #ffde2a;
    height: 85vh;
}

.player{
    background-color: #840331;
    height: 200px;
    width: 150px;
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    border: 10px solid #000000;
    border-bottom: none;
    border-radius: 60px 80px 0 0;
    z-index: 1;
}

.player:after{
    content: '';
    position: absolute;
    width: 92%;
    height: 85%;
    background-color: #ea1616;
    top: 2.5px;
    left: 6px;
    border-radius: 58% 70% 28% 42% / 28% 56% 88% 89%;
}

.legs{
    height: 50px;
    width: 60px;
    background-color: #840331;
    position: absolute;
    bottom:  -50px;
    left: -10px;
    border: 10px solid #000000;
    border-top: none;
    border-radius: 0 0 22px 22px;
    z-index: 1;
}

.legs:before{
    content: "";
    height: 36px;
    width: 42px;
    background-color: #840331;
    position: absolute;
    
    right: -100px;
    border: 10px solid #000000;
    border-top: none;
    border-radius: 0 0 22px 22px;
}

.greenlegs{
    background-color: #0a4d2e;
}

.greenlegs:before{
    background-color: #0a4d2e;
}

.legs:after{
    content: "";
    height: 10px;
    width: 55px;
    background-color: #000000;
    position: absolute;
    top: -10px;
    left: 40px;
    border-radius: 0 0 10px 0 ;
}

.back{
    height: 120px;
    width: 35px;
    background-color: #ea1616;
    border: 10px solid black;
    position: absolute;
    left: -45px;
    top: 65px;
    border-right: none;
    border-radius: 20px 0 0 20px;
}

.back:before{
    content: '';
    position: absolute;
    height: 78px;
    width: 26px;
    background-color: #840331;
    bottom: -0.5px;
    left: -1px;
    border-radius: 12px 0 0 8px;
}

.greenback{
    background-color: #117f2d;
}

.greenback:before{
    background-color: #0a4d2e;
}

.glass{
    height: 75px;
    width: 110px;
    background-color: #225381;
    z-index: 2;
    position: absolute;
    top: 30px;
    left: 40px;
    border-radius: 25px 50px 30px 30px;
    border: 10px solid black ;
}

.glass:before{
    content: '';
    position: absolute;
    width: 85%;
    height: 65%;
    background-color: #71d4ec;
    border-radius: 0 28px 3px 30px;
    right: 0;
    top: 0;
}
.glass:after{
    content: '';
    position: absolute;
    width: 45%;
    height: 22%;
    background-color: #f7f7f7;
    left: 39px;
    top: 5px;
    border-radius: 10px ;
    -webkit-transform: rotate(6deg);
            transform: rotate(6deg);
}

.shadow{
    height: 40px;
    width: 210px;
    background-color: rgba(153, 130, 0, 0.2);
    position: absolute;
    bottom: -60px;
    right: -40px;
    border-radius: 50%;
    z-index: 0;
}

.green{
    background-color: #0a4d2e;
}

.green:after{
    background-color: #117f2d;
}

.options{
    display: flex;
    height: 15vh;
    width: 100%;
    justify-content: center;
    background-color: #ffde2a;
}

button{
    margin: 20px;
    /* padding: 10px; */
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: none;
    outline: none;
    /* box-shadow: 0 0 4px 4px #ffd900; */
    color: #ea1616;
    cursor: pointer;
}

button:hover{
    box-shadow: 0px 0px 16px 18px #ffde2a;
}

.link-a{
    padding-top: 4px;
    color: white;
    transition: 1s;
}

.link{
    background-color: #0077b5;
    color: white;
    font-size: 20px;
    
}

.git{
    background-color: rgb(36, 35, 35);
    font-size: 20px;
}

.link-a:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
